import { WebGLRenderParams, WebGLSceneOptions } from "components/WebGL/gl";
import { WebGLScene } from 'components/WebGL/three/glScene';
import { damp } from "maath/easing";
import emitter, { EVENTS } from "plugins/store/events";
import { mainWebGLRenderer } from "plugins/webgl/three";
import { calculateUnitSize, getPixelSize } from "plugins/webgl/utils";
import { AmbientLight, BackSide, Box3, BoxGeometry, CompressedTexture, DirectionalLight, DoubleSide, FrontSide, Group, LinearToneMapping, Mesh, MeshBasicMaterial, MeshStandardMaterial, NearestFilter, Object3D, PerspectiveCamera, PlaneGeometry, PMREMGenerator, Raycaster, SRGBColorSpace, Texture, Vector2, Vector3 } from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { RoomEnvironment } from 'three/examples/jsm/environments/RoomEnvironment.js';
import { store as loaderStore } from 'ui/Loader/store';
import { gsap } from "vendors/gsap";

interface InteractiveMeshes {
  disc?: Mesh;
  caseFront?:Object3D;
  caseBack?:Object3D;
  article?: Group;
  click?: Mesh;
}

const DELAY_CLICK = 350;

export class CDScene extends WebGLScene {
 
  handlers : Obj;
  mouse : Vector2 = new Vector2();

  moveArticle = false;

  raycaster : Raycaster;

  clickTimers = { start:0, end:0 };
  isDown = false;

  container : Group;
  interactiveMesh : InteractiveMeshes = {}

  lights : Obj = {};
  
  status = {
    toggle: false,
    reduce: false,
  }

  orbitControls : OrbitControls;
  orbitDefaultState: Obj = {};

  pmremGenerator : PMREMGenerator

  constructor(options:WebGLSceneOptions) {
    // new glScene(options)
    super(options);

    mainWebGLRenderer!.toneMapping = LinearToneMapping;

    this.handlers = {
      clickDown: this.onClickDown.bind(this),
      click: this.onClick.bind(this),
      move: this.onMouseMove.bind(this),
    }

    this.container = new Group();
    this.container.position.x = -this.container.scale.x / 2;
    this.container.visible = false;
    this.orbitControls = new OrbitControls(this.getActiveCamera(), mainWebGLRenderer!.domElement);
    this.orbitControls.enableDamping = true;
    this.orbitControls.dampingFactor = 0.1
    this.orbitControls.enablePan = false;
    this.orbitControls.enableZoom = false;
    
    this.pmremGenerator = new PMREMGenerator(mainWebGLRenderer!);
		this.pmremGenerator.compileEquirectangularShader();
    this.scene.environment = this.pmremGenerator.fromScene(new RoomEnvironment()).texture;
    this.scene.environmentIntensity = .25; 

    this.raycaster = new Raycaster();

    return this;
  }

  async build() {
    this.scene.add(this.container);
   
    const model = loaderStore.getModel('cd').clone();
    model.name = "cd"
    model.rotation.y = -Math.PI / 2;
    model.scale.multiplyScalar(.7);
    this.container.add(model);
   
    model.traverse( (child) => {
      const c = child as Mesh;
      // this one for translating the disc
      if(c.name === "CD_1") {
        this.interactiveMesh.disc = c;
        this.interactiveMesh.disc.position.set(0, 0, 0)
      }

      // this one for translating the front
      if(c.name === "Case_Front_0") {
        this.interactiveMesh.caseFront = c;
        this.interactiveMesh.caseFront.rotation.y = 0;
      }

       // this one for translating the back
       if(c.name === "Case_Back_2") {
        this.interactiveMesh.caseBack = c;
      }
    });

    //center the model
    const box = new Box3().setFromObject(this.container);
    const size = box.getSize(new Vector3());
    model.position.y = -size.y / 2;
    model.position.x = size.x / 2;

    //---assign textures
    const coverFront = await loaderStore.getLoader("texture")?.loadAsync(this.settings?.folder + this.settings?.recto);
    if(coverFront) {
      this.prepareTexture(coverFront)
      
      const frontMesh = this.interactiveMesh.caseFront?.getObjectByName("Object_11") as Mesh;
      const frontMaterial = frontMesh.material as MeshStandardMaterial;
      frontMaterial.map = coverFront;
      frontMaterial.side = BackSide;
      
      const translucent = this.interactiveMesh.caseFront?.getObjectByName("Object_10") as Mesh;
      const translucentMaterial = translucent?.material as MeshStandardMaterial;
      
      const newMat = new MeshStandardMaterial().copy(translucentMaterial);
      newMat.opacity = 1;
      newMat.side = DoubleSide;
      newMat.color.setStyle("#3f3f3f");
      translucent.material = newMat;
    }

    const coverBack = await loaderStore.getLoader("texture")?.loadAsync(this.settings?.folder + this.settings?.verso);
    if(coverBack) {
      this.prepareTexture(coverBack);

      const neutralMat = new MeshStandardMaterial({color:0x3F3F3F, roughness:0.5, metalness:0.5});      
      const backMaterial = new MeshStandardMaterial({side:FrontSide, map:coverBack})
      const backMesh = new Mesh(new BoxGeometry(1,1, 0.015), [
        neutralMat,
        neutralMat,
        neutralMat,
        neutralMat,
        neutralMat,
        backMaterial
      ]);

      backMesh.rotation.y = Math.PI / 2;
      backMesh.position.x = -0.02;
      backMesh.position.z = -0.1;
      backMesh.scale.multiplyScalar(1.97);
      this.interactiveMesh.caseBack?.add(backMesh);

      const translucent = this.interactiveMesh.caseBack?.getObjectByName("Object_5") as Mesh;
      if(translucent) {
        (translucent.material as MeshStandardMaterial).opacity = .05;
      }      
    }

    //--- Add Article
    if(this.settings?.article) {
      const articleGroup = new Group();
      this.container.add(articleGroup);
      this.interactiveMesh.article = articleGroup;

      const isArray = !(typeof this.settings?.article === 'string');
      const articleUrl = this.settings?.folder + (isArray ? this.settings?.article[0] : this.settings?.article);
      const articleTexture = await loaderStore.getLoader("texture")?.loadAsync(articleUrl);
      if(articleTexture) {
        this.prepareTexture(articleTexture, false);

        const whiteMat = new MeshBasicMaterial({color:0xFFFFFF});
        const article = new Mesh(new BoxGeometry(1,1, 0), [
          whiteMat, whiteMat, whiteMat, whiteMat,
          new MeshBasicMaterial({
            map: articleTexture,
          }),
          whiteMat
        ]);
    
        const ratioArticle = articleTexture.image.width / articleTexture.image.height;
        article.name = "article";
        article.scale.set(this.settings?.article_scale, this.settings?.article_scale / ratioArticle, this.settings?.article_scale).multiplyScalar(.5);
        articleGroup.position.z = -0.04;
        articleGroup.position.x = 0.2;
        articleGroup.add(article);

        if(this.settings?.article_offset) {
          articleGroup.position.x += this.settings?.article_offset;
        }

        // add interaction
        const plane = new Mesh(new PlaneGeometry(1,1), new MeshBasicMaterial({transparent:true, opacity:0}));
        plane.name = "click";
        plane.userData.x = plane.position.x;
        plane.scale.set(.5, article.scale.y, 1);
        plane.userData.endX = plane.position.x + ((article.scale.x/2) - (plane.scale.x/2));
        
        this.interactiveMesh.click = plane;
        articleGroup.add(plane);
      }
    }
    
    //--- Add Lights
    this.lights = {
      ambient: new AmbientLight(0xffffff, 3),
      directional: new DirectionalLight(0xFFFFFF, 1),
    }
    
    this.container.add(this.lights.ambient);    
    this.lights.directional.position.set(-3, 2, 7.5);
    this.container.add(this.lights.directional); 

    emitter.emit(EVENTS.OBJECT_DETAIL_READY);

    //save initial state of the orb controls
    this.orbitDefaultState.target = this.orbitControls.target.clone();
    this.orbitDefaultState.cam = this.getActiveCamera().position.clone();

    this.addEvents();
  }

  addEvents() {
    mainWebGLRenderer?.domElement.addEventListener("pointerdown", this.handlers.clickDown);
    mainWebGLRenderer?.domElement.addEventListener("pointerup", this.handlers.click);
    mainWebGLRenderer?.domElement.addEventListener("mousemove", this.handlers.move);
  }
  
  removeEvents() {
    mainWebGLRenderer?.domElement.removeEventListener("pointerdown", this.handlers.clickDown);
    mainWebGLRenderer?.domElement.removeEventListener("pointerup", this.handlers.click);
    mainWebGLRenderer?.domElement.removeEventListener("mousemove", this.handlers.move);
  }

  // Resize is called by default (by the plugin) when the scene is created
  resize() {
    // glScene.resize()
    super.resize();
  }

  render(params:WebGLRenderParams) {
  
    if(!this.scene.children.length) {
      super.render(params);
      this.build();
    }
    else{
      super.render(params);

      if(this.interactiveMesh.disc) {
        damp(this.container.rotation, 'y', !this.status.toggle ? 0 : -Math.PI / 32, .2, params.delta);
        damp(this.interactiveMesh.caseFront!.rotation, 'y', !this.status.toggle ? 0 : -Math.PI / 8, .2, params.delta);
        
        if(this.moveArticle) {
          if(this.interactiveMesh.article) {
            damp(this.interactiveMesh.article.position, 'z', this.status.toggle ? 0.06 : -0.08, .25, params.delta);
            damp(this.interactiveMesh.article.position, 'x', this.status.toggle ? (this.settings?.article_offset  ?? 0) + 0.8 : (this.settings?.article_offset  ?? 0) + 0.2, .25, params.delta);
          
            damp(this.interactiveMesh.click!.position, 'x', this.status.toggle ? this.interactiveMesh.click!.userData.endX : this.interactiveMesh.click!.userData.x, .25, params.delta);
          }
          
          damp(this.interactiveMesh.disc.position, 'z', this.status.toggle ? -1 : 0, .25, params.delta);
          damp(this.interactiveMesh.disc.position, 'x', this.status.toggle ? 0.1 : 0, .25, params.delta);
        }
      }
    }

    this.raycaster.setFromCamera(this.mouse, this.getActiveCamera());
    this.orbitControls.update();
  }

  dispose() {
    // glScene.dispose()
    // dispose every geometry and material for every Mesh children
    this.disposeGeometries(this.container);
    this.interactiveMesh = {};
    super.dispose(); 
    this.removeEvents();

    document.body.style.cursor = 'default';
  }

  // Click Down
  onClickDown() : void {
    this.clickTimers.start = Date.now(); 
    this.clickTimers.end = 0; 
    this.isDown = true;
  }

  // Click Up
  onClick(e:MouseEvent) : void {
    this.clickTimers.end = Date.now();
    this.isDown = false;
    
    const isRealClick = this.clickTimers.end - this.clickTimers.start;
    if(isRealClick <= DELAY_CLICK) {
      if(this.settings?.article && this.interactiveMesh.article) {
        this.checkIntersection(e, "click");
      }
    }  
  }

  onMouseMove(e:MouseEvent) : void {
    this.getMousePosition(e);

    if(this.settings?.article && this.interactiveMesh.article) {
      this.checkIntersection(e, "move");
    }
  }

  // Get The Real Mouse Position with the offset position to the top left point
  getMousePosition(evt : MouseEvent) {
    const el = evt.currentTarget as HTMLElement;
    const bounds = el.getBoundingClientRect();

    let x = 0,
        y = 0;

    x = evt.clientX - bounds?.left;
    y = evt.clientY - bounds?.top;

    this.mouse.x = (x / mainWebGLRenderer!.domElement.offsetWidth) * 2 - 1;
    this.mouse.y = -(y / mainWebGLRenderer!.domElement.offsetHeight) * 2 + 1;
  }

  show() {
    this.container.visible = true;
  }

  toggleShowingInside(value: boolean) {
    this.status.toggle = value;

    setTimeout( () => {
      this.moveArticle = value
    }, 400)
  }

  prepareTexture(tex : Texture | CompressedTexture, noMipmaps = true) {
    tex.colorSpace = SRGBColorSpace;
    if(noMipmaps) {
      tex.generateMipmaps = false;
      tex.minFilter = NearestFilter;
    }
  }

  checkIntersection(e:MouseEvent, type:string = "click") {
    if(this.status.reduce && !this.status.toggle) return;

    // calculate objects intersecting the picking ray
	  const intersection = this.raycaster.intersectObject( this.interactiveMesh.click as Mesh );

    //change body cursor
    if(type === 'move') {
      const hasIntersection = intersection.length > 0;
      document.body.style.cursor = hasIntersection ? 'pointer' : 'default';

      const article = this.interactiveMesh.article?.getObjectByName("article") as Mesh;
      if(article && !this.isDown){
        gsap.to(article.position, {x:hasIntersection ? 0.1 : 0, ease:"power3.out", duration:1});
      }
    }

    //handle the click and reduce the scene
    if(intersection.length && type === 'click') this.goMiniature();
  }

  // Reduce the scene
  goMiniature() {
    this.orbitControls.enabled = false;
    this.status.reduce = true;
    this.toggleShowingInside(false);

    const article = this.interactiveMesh.article?.getObjectByName("article") as Mesh;
    if(article){
      gsap.to(article.position, {x:0, ease:"power3.out", duration:1});
    }

    document.body.style.cursor = "default";

    const size = calculateUnitSize(20, this.camera as PerspectiveCamera);

    gsap.timeline()
      .to(this.getActiveCamera().position, {z:20, x:this.orbitDefaultState.cam.x, y:this.orbitDefaultState.cam.y, ease:"power4.inOut", duration:1.5}, 0)
      .to(this.orbitControls.target, {z:this.orbitDefaultState.target.z, y:this.orbitDefaultState.target.y, x:this.orbitDefaultState.target.x, ease:"power4.inOut", duration:1.5}, 0)
      .to(this.container.position, {
        x:-(size.width / 2) + (this.container.scale.x * .75),
        y:(size.height / 2) - (this.container.scale.y * 1), ease:"power4.inOut", duration:1.5}, .25)
      .add( () => {
        const {width, height} = getPixelSize(this.container, this.camera);
        emitter.emit(EVENTS.OBJECT_SHOW_ARTICLE, {width, height});
      }, 1.5);
  }

  // Revert to the interactive scene
  revertToInteractive() {
    gsap.timeline()
    .to(this.getActiveCamera().position, {z:5, ease:"power4.inOut", duration:1.5}, .25)
    .to(this.container.position, {
      x:-this.container.scale.x / 2,
      y:0, ease:"power4.inOut", duration:1.5}, 0)
    .add( () => {
      this.status.reduce = false;
      this.toggleShowingInside(true);
      this.orbitControls.enabled = true;
    }, 1.3)

  }
}

